.header {
    width: 100%;
}

.header__top {
    height: rem(35px);
    border-bottom: 1px solid $line;
    display: flex;
}

.header__top-left {
    flex-grow: 1;
}

.header__top-right {
    flex-shrink: 0;
    text-align: center;


    @include screen-sm {
        display: none;
        overflow: hidden;
    }

}

.header__bottom {
    height: rem(126px);
}

.header__container {
    @include screen-md {
        display: flex;
        justify-content: space-between;
    }
}

.header__column {
    width: 25%;
    height: 100%;
    display: flex;
    align-items: center;

    @include screen-md {
        width: initial;
        max-width: 25%;
    }

    &:nth-child(1),
    &:nth-child(4) {
        @include screen-sm {
            max-width: 50%;
        }
    }

    &:nth-child(2),
    &:nth-child(3) {
        @include screen-sm {
            display: none;
            overflow: hidden;
        }
    }
        
}